<template>
  <div>
    <a-modal :dialog-style="{ top: '20px' }" v-model="dialog_addMeeting" title="จองห้องประชุม" @ok="handleOk">
      <a-form :form="form" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
        <a-form-item label="ชื่อ-นามสกุล :">
          <a-input
            v-model="username"
            v-decorator="[
              'username',
              {
                rules: [{ required: true, message: 'โปรดระบุชื่อ-นามสกุล' }],
              },
            ]"
            placeholder="ชื่อ-นามสกุล"
          />
        </a-form-item>
        <a-form-item label="เลือกห้องประชุม :">
          <a-select
            default-value="ห้อง 100"
            style="width: 200px"
            v-model="room_Meeting"
          >
            <a-select-opt-group>
              <span slot="label"><a-icon class="pr-2" type="user" />ห้องประชุมใหญ่</span>
              <a-select-option value="room100">
                ห้อง 100
              </a-select-option>
              <a-select-option value="room101">
                ห้อง 101
              </a-select-option>
            </a-select-opt-group>
            <a-select-opt-group >
            <span slot="label"><a-icon class="pr-2" type="robot" />ห้องประชุมเล็ก</span>
              <a-select-option value="room200">
                ห้อง 200
              </a-select-option>
            </a-select-opt-group>
          </a-select>
        </a-form-item>

        <a-form-item label="เริ่มการประชุม(วันที่-เวลา)">
          <a-date-picker
            v-decorator="['date-time-picker', config]"
            format="YYYY-MM-DD HH:mm:ss"
            :disabled-date="disabledDate"
            show-time
          />
        </a-form-item>

        <a-form-item label="สิ้นสุดการประชุม(วันที่-เวลา)">
          <a-date-picker
            v-decorator="['date-time-picker', config]"
            format="YYYY-MM-DD HH:mm:ss"
            :disabled-date="disabledDate"
            show-time
          />
        </a-form-item>
        <a-form-item label="หัวข้อการประชุม:">
          <a-input
            v-decorator="[
              'agenda',
              {
                rules: [{ required: true, message: 'โปรดระบุหัวข้อการประชุม' }],
              },
            ]"
            placeholder="โปรดระบุหัวข้อการประชุม"
          />
        </a-form-item>
      </a-form>

      <template slot="footer">
        <a-button key="back" @click="CloseDialog()">
          ยกเลิก
        </a-button>
        <a-button
          key="submit"
          type="primary"
          :loading="loading"
          @click="API_SendAddData()"
        >
          ยืนยัน
        </a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
import moment from "moment";

const formItemLayout = {
  labelCol: { span: 2 },
  wrapperCol: { span: 4 },
};
const formTailLayout = {
  labelCol: { span: 2 },
  wrapperCol: { span: 4, offset: 2 },
};
export default {
  components: {},
  data: () => ({
    dialog_addMeeting: false,
    data_date: "",
    formItemLayout,
    formTailLayout,
    username:"",
    room_Meeting:"",
    loading:false
  }),
  methods: {
    open(data) {
      console.log(data);
      this.data_date = data;
      this.dialog_addMeeting = true;
    },
    CloseDialog(e) {
      console.log(e);
      this.dialog_addMeeting = false;
    },
    disabledDate(current) {
      // Can not select days before today and today
      return current && current < moment().endOf("day");
    },
    moment,
    range(start, end) {
      const result = [];
      for (let i = start; i < end; i++) {
        result.push(i);
      }
      return result;
    },

    API_SendAddData() {},
  },
};
</script>
